.alert-update-app {
    top: 0;
    position: absolute;
    width: 100%;
    z-index: 20;
  }
  
  .alert-update-app-wrapper {
    align-items: center;
    background: rgba(0, 0, 0, 0.65);
    border-radius: 10px;
    color: white;
    display: flex;
    font-size: 15px;
    font-weight: bold;
    justify-content: space-between;
    margin: 15px 15px 10px 15px;
    padding: 10px 20px;
  }
  
  .alert-update-app-emoji {
    margin-left: 5px;
  }
  
  .alert-update-button {
    background-color: gray;
    border-radius: 10px;
    border: 2px solid white;
    color: white;
    cursor: pointer;
    font-size: 15px;
    padding: 5px 10px;
  }