.container {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%;
  }
  
  .screen {
    align-items: center;
    background-color: var(--principal);
    display: flex;
    height: var(--base-height);
    justify-content: center;
    overflow: hidden;
    position: relative;
    width: var(--base-width);
  }